import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { EventsService } from 'src/app/services/events.service';
import { LocalService } from 'src/app/services/local.service';
import { RestService } from 'src/app/services/rest.service';
import { UtilityService } from 'src/app/services/utility.service';

@Component({
  selector: 'app-cleanerpayments',
  templateUrl: './cleanerpayments.page.html',
  styleUrls: ['./cleanerpayments.page.scss'],
})
export class CleanerpaymentsPage implements OnInit {
  loggedCleaner: any;
  report: any;
  month: any;
  year: any;
  months: any = [];
  currentMonth: any;
  currency: any;
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {
    this.loggedCleaner = this.localService.getCleaner();
    this.currency = this.localService.getSettings().currency_symbol;
    // set current year & month by default
    this.year = new Date().getFullYear();
    this.month = ('0' + (new Date().getMonth() + 1)).slice(-2);
    // console.log(this.month, this.year);
    //months
    this.months = [
      { id: '0', name: 'January' },
      { id: '1', name: 'February' },
      { id: '2', name: 'March' },
      { id: '3', name: 'April' },
      { id: '4', name: 'May' },
      { id: '5', name: 'June' },
      { id: '6', name: 'July' },
      { id: '7', name: 'August' },
      { id: '8', name: 'September' },
      { id: '9', name: 'October' },
      { id: '10', name: 'November' },
      { id: '11', name: 'December' },
    ];

    this.currentMonth = new Date().getMonth();
    // select current month
    this.month = this.currentMonth;
    //console.log(this.month);
    this.utilityService.loadingOpen('Loading...');
    this.restService
      .postData('cleaner/' + this.loggedCleaner.id + '/transactions', {
        month: this.month,
      })
      .subscribe(
        (data) => {
          this.utilityService.loadingClose();
          console.log(data);
          this.report = data;
        },
        (error) => {}
      );
  }

  ngOnInit() {}

  refreshData() {
    this.currentMonth = this.month;
    this.utilityService.loadingOpen('Updating...');
    this.restService
      .postData('cleaner/' + this.loggedCleaner.id + '/transactions', {
        month: this.month,
      })
      .subscribe(
        (data) => {
          this.utilityService.loadingClose();
          console.log(data);
          this.report = data;
        },
        (error) => {}
      );
  }

  openNotifications() {
    this.router.navigate(['/cleanernotifications']);
  }
}
